	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"NPD Ban Backfires","date":"23 Jan 2002","slug":"/2002-01-23-npd-ban-backfires/","tags":"Y2002"},"html":"<p>AP: <a href=\"http://www.expatica.com/germanymain.asp?pad=190,205,&amp;item_id=18772\">Germany's highest court calls off hearings on banning far-right party</a>.  The government's application to ban the neo-Nazi NPD has been postponed by the court, as it cites statements by a party leader who was a government informant. The NPD ban is just a PR stunt - noone truly believes the ban would significantly affect neo-Nazi activity. But now the stunt has totally backfired, and Interior Minister Schily is in hot water.</p>"},"next":{"fields":{"title":"Little Pink Toasters","date":"22 Jan 2002","slug":"/2002-01-22-little-pink-toasters/"}},"previous":{"fields":{"title":"They're Getting What They Deserve","date":"23 Jan 2002","slug":"/2002-01-23-theyre-getting-what-they-deserve/"}}},{"node":{"fields":{"title":"Little Pink Toasters","date":"22 Jan 2002","slug":"/2002-01-22-little-pink-toasters/","tags":"Y2002"},"html":"<p>Dave's comments today about whining Radio users and about free toasters the other day got me thinking about <a href=\"http://radio.weblogs.com/0102073/2002/01/22.html\">open source toasters</a>.</p>"},"next":{"fields":{"title":"In This Corner... And In This One","date":"22 Jan 2002","slug":"/2002-01-22-in-this-corner-and-in-this-one/"}},"previous":{"fields":{"title":"NPD Ban Backfires","date":"23 Jan 2002","slug":"/2002-01-23-npd-ban-backfires/"}}},{"node":{"fields":{"title":"In This Corner... And In This One","date":"22 Jan 2002","slug":"/2002-01-22-in-this-corner-and-in-this-one/","tags":"Y2002"},"html":"<p>It was actually yesterday, but yesterday the Greens announced that they would for the first time field a main candidate, namely Joschka Fischer. No gender balanced duo this time. Leave it to the PDS to present a gender balanced quartet. But not geographically balanced... they want 30% of the the vote in the east and a whole 2% in the west.</p>"},"next":{"fields":{"title":"Delusions Of Grandeur","date":"22 Jan 2002","slug":"/2002-01-22-delusions-of-grandeur/"}},"previous":{"fields":{"title":"Little Pink Toasters","date":"22 Jan 2002","slug":"/2002-01-22-little-pink-toasters/"}}}],"pathPrefix":"","first":false,"last":false,"index":939,"pageCount":1101}}

